<script>
import {
  GET_COMMUNE,
  GET_DECLARATIONS,
  GET_DECLARATION_TYPES
} from "@/core/store/app.module";
import { mapState } from "vuex";
export default {
  name: "Step-2",
  data: () => ({
    form: {
      categorieDeProjet: "",
      leTypeDeDeclaration: "",
      declaration: "",
      surfacePlancherCouverts: "",
      descriptionProjet: "",
      attestation: "",
      prefectureOuProvince: "",
      commune: "",
      arrondissement: "",
      adresseDuProjet: ""
    },
    processing: false,
    
    arrondissementOptions: [
      {
        value: "",
        text: "Slectionnez Arrondissement"
      }
    ],
  }),
  methods: {
    nextStep() {
      this.$emit("go-next");
    },
    prevStep() {
      this.$emit("go-back");
    },
    submitForm() {
      this.nextStep();
    },
    async changeProvince(id) {
      this.processing = true;
      this.form.categorieDeProjet = "";
      await this.$store.dispatch(GET_COMMUNE, id);
      this.processing = false;
    },
    async ChangeCat(id) {
      this.processing = true;
      this.form.leTypeDeDeclaration = "";
      this.form.declaration = "";
      await this.$store.dispatch(GET_DECLARATIONS, id);
      await this.$store.dispatch(GET_DECLARATION_TYPES, id);
      this.processing = false;
    }
  },
  computed: {
    ...mapState({
      categorieDeProjetOptions: state => [
        { value: "", text: "Veuillez selectionnez" },
        ...state.app.selectOptions.projectCats
      ],
      communeOptions: state => [
        { value: "", text: "Veuillez selectionnez" },
        ...state.app.selectOptions.commune
      ],
      prefectureOuProvinceOptions: state => [
        { value: "", text: "Veuillez selectionnez" },
        ...state.app.selectOptions.prefectureOuProvince
      ],
      leTypeDeDeclarationOptions: state => [
        { value: "", text: "Veuillez selectionnez" },
        ...state.app.selectOptions.declarationTypes
      ],
      declarationOptions: state => [
        { value: "", text: "Veuillez selectionnez" },
        ...state.app.selectOptions.declarations
      ],
    })
  }
};
</script>
