        </p>
      </div>
      <div class="col-6">
        <vue-dropzone
          ref="myVueDropzone"
          :useCustomSlot="true"
          id="dropzone"
          @vdropzone-upload-progress="e => uploadProgress(e, 1)"
          :options="dropzoneOptions1"
          @vdropzone-file-added="e => fileAdded(e, 1)"
          @vdropzone-sending-multiple="e => sendingFiles(e, 1)"
          @vdropzone-success="e => success(e, 1)"
        >
          <div class="dropzone-custom-content">
