<script>
import vue2Dropzone from "vue2-dropzone";
import "vue2-dropzone/dist/vue2Dropzone.min.css";
export default {
  data: () => ({
    error: "",
    files: {
      left: [],
      right: []
    },
    dropzoneOptions1: {
      url: "https://httpbin.org/post",
      maxFilesize: 42,
      dictDefaultMessage:
        "Glisser ou double click pour charger les fichiers de plan +",
      thumbnailWidth: 140,
      thumbnailHeight: 140,
      uploadMultiple: false
    },
    dropzoneOptions2: {
      url: "https://httpbin.org/post",
      maxFilesize: 42,
      dictDefaultMessage:
        "Glisser ou double click pour charger le fichier de contrat +",
      thumbnailWidth: 140,
      thumbnailHeight: 140,
      uploadMultiple: false
    }
  }),
  components: {
    vueDropzone: vue2Dropzone
  },
  name: "Step-4",
  methods: {
    paramName() {
      return "file[]";
    },
    uploadProgress() {
      console.log("uploadProgress | test");
    },

    fileAdded(e, x) {
      console.log("fileAdded | added file", x);
      console.log("fileAdded | for input", e);
    },

    sendingFiles() {
      console.log("sendingFiles | sending files");
    },

    success(e, x) {
      if (x === 1) {
        this.files.left.push(e);
      } else {
        this.files.right.push(e);
      }
      console.log("success | this.files", this.files);
    },


    nextStep() {
      if (this.files.left.length < 1 || this.files.left.length < 1) {
        this.error = "Please upload the files first.";
        setTimeout(() => {
          this.error = "";
        }, 2000);
        return false;
      }
      this.$emit("go-next");
    }
  }
};
