<template>
  <div class="first-step step">
    <div class="row">
      <div class="col-12">
        <div class="home__stage mt-3 step__block">
          <div class="step__block__header">
            <div class="step__block__header__content">
              Les Rgimes fonciers
            </div>
          </div>
          <form @submit.prevent="submitForm">
            <div class="w-100 step__input">
              <!-- Field 1 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1"
                    >Rgime foncier
                  </label>
                </div>
                <div class="col-7">
                  <b-form-select
                    class="step__input__select"
                    v-model="form.regimeFoncier"
                    :options="regimeFoncierOptions"
                  ></b-form-select>
                </div>
              </div>
              <!-- Field 1 end -->
            </div>

            <div class="w-100 step__input">
              <!-- Field 5 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1"
                    >Statut foncier</label
                  >
                </div>
                <div class="col-7">
                  <b-form-select
                    class="step__input__select"
                    v-model="form.statutFoncier"
                    :options="statutFoncierOptions"
                  ></b-form-select>
                </div>
              </div>
              <!-- Field 5 end -->
            </div>
            <div class="w-100 step__input">
              <!-- Field 5 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1"
                    >Rfrence du supportFoncier</label
                  >
                </div>
                <div class="col-7">
                  <b-form-input
                    class="step__input__text"
                    v-model="form.supportFoncier"
                    type="text"
                    placeholder="Rfrence du support Foncier"
                  />
                </div>
              </div>
              <!-- Field 5 end -->
            </div>
            <div class="w-100 text-right">
              <button type="submit" class="button button--green">
                Enregistrer et ajouter un autre Matre d'ouvrage
              </button>
            </div>
          </form>
        </div>
      </div>
      <div class="col-12">
        <div class="home__stage step__block mt-3">
          <div class="step__block__header">
            <div class="step__block__header__content">
              Matre douvrage : Informations
            </div>
          </div>
          <table class="text-center table table-bordered">
            <thead>
              <tr style="background-color: #d58512;color: white;">
                <th class="text-center">N</th>
                <th class="text-center">Rgime foncier</th>
                <th class="text-center">Support foncier</th>
                <th class="text-center">Statut foncier</th>
                <th class="text-center" style="width:10%">Supprimer</th>
              </tr>
            </thead>
            <tbody>
              <template v-for="(project, index) in projects">
                <tr :key="index">
                  <td class="text-center">{{ index + 1 }}</td>

                  <td class="text-center">{{ project.regimeFoncier }}</td>
                  <td class="text-center">{{ project.supportFoncier }}</td>
                  <td class="text-center">{{ project.statutFoncier }}</td>
                  <td class="text-center">
                    <button class="button button--green" @click="remove(index)">
                      Supprimer
                    </button>
                  </td>
                </tr>
              </template>
            </tbody>
          </table>
        </div>
      </div>
    </div>
    <div class="row mt-4">
      <div class="col-6 text-left">
        <a
          href="javascript:void(0)"
          @click="prevStep"
          class="button button--blue"
          >Prcdent</a
        >
      </div>
      <div class="col-6 text-right">
        <a
          href="javascript:void(0)"
          @click="nextStep"
          class="button button--blue"
          >suivant</a
        >
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "Step-3",
  data: () => ({
    projects: [],
    form: {
      regimeFoncier: "",
      statutFoncier: "",
      supportFoncier: ""
    }
  }),

  methods: {
    remove(index) {
       this.projects = this.projects.filter(
        (owner, indx) => owner && index !== indx
      );
    },
    nextStep() {
      this.$emit("go-next");
    },
    prevStep() {
      this.$emit("go-back");
    },
    submitForm() {
      const temp = {
        regimeFoncier: "",
        statutFoncier: "",
        supportFoncier: ""
      };
      const newVar = Object.assign({}, this.form);
      this.projects.push(newVar);
      this.form = temp;
    }
  },
  computed: {
    ...mapState({
      statutFoncierOptions: state => [
        { value: "", text: "Veuillez Statut foncier" },
        ...state.app.thirdStepOptions.statutFoncier
      ],
      regimeFoncierOptions: state => [
        { value: "", text: "Veuillez slectionnez le rgime foncier" },
        ...state.app.thirdStepOptions.regimeFoncier
      ]
    })
  }
};
</script>

<style lang="scss" scoped></style>
