    <div class="row">
      <div class="col-12">
        <div class="home__stage mt-3 step__block">
          <div class="step__block__header">
            <div class="step__block__header__content">
              Matre douvrage : Informations
            </div>
          </div>
          <form @submit.prevent="submitForm">
            <div class="w-100 step__input">
              <!-- Field 1 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1"
                    >Matre douvrage</label
                  >
                </div>
                <div class="col-7">
                  <b-form-select
                    class="step__input__select"
                    v-model="form.maitredouvrage"
                    :options="maitreDouvrageOptions"
                    required
                  ></b-form-select>
                </div>
              </div>
              <!-- Field 1 end -->
            </div>
            <div
              class="w-100 step__input"
              v-if="
                form.maitredouvrage === '05_Socit Commerciale' ||
                  form.maitredouvrage === '07_Collectivit Locale'
              "
            >
              <!-- Field 2 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1"
                    >Denomination</label
                  >
                </div>
                <div class="col-7">
                  <b-form-input
                    class="step__input__text"
                    v-model="form.denomination"
                    type="text"
                    required
                  ></b-form-input>
                </div>
              </div>
              <!-- Field 2 end -->
            </div>
            <div
              class="w-100 step__input"
              v-if="
                form.maitredouvrage === '' ||
                  form.maitredouvrage === '01_Monsieur'
              "
            >
              <!-- Field 2 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1">Nom</label>
                </div>
                <div class="col-7">
                  <b-form-input
                    class="step__input__text"
                    v-model="form.nom"
                    type="text"
                    required
                  ></b-form-input>
                </div>
              </div>
              <!-- Field 2 end -->
            </div>
            <div
              class="w-100 step__input"
              v-if="form.maitredouvrage === '01_Monsieur'"
            >
              <!-- Field 2 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1"
                    >CIN/Passeport</label
                  >
                </div>
                <div class="col-7">
                  <b-form-input
                    class="step__input__text"
                    v-model="form.cinPasseport"
                    type="text"
                    required
                  ></b-form-input>
                </div>
              </div>
              <!-- Field 2 end -->
            </div>
            <div
              class="w-100 step__input"
              v-if="form.maitredouvrage === '01_Monsieur'"
            >
              <!-- Field 2 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1">Prenom</label>
                </div>
                <div class="col-7">
                  <b-form-input
                    class="step__input__text"
                    v-model="form.prenom"
                    type="text"
                    required
                  ></b-form-input>
                </div>
              </div>
              <!-- Field 2 end -->
            </div>
            <div
              class="w-100 step__input"
              v-if="form.maitredouvrage === '05_Socit Commerciale'"
            >
              <!-- Field 3 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1"
                    >Registre numero</label
                  >
                </div>
                <div class="col-7">
                  <b-form-input
                    class="step__input__text"
                    v-model="form.registreNumbero"
                    type="text"
                    required
                  ></b-form-input>
                </div>
              </div>
              <!-- Field 3 end -->
            </div>
            <div class="w-100 step__input">
              <!-- Field 4 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1"
                    >Adresse</label
                  >
                </div>
                <div class="col-7">
                  <b-form-input
                    class="step__input__text"
                    v-model="form.adresse"
                    type="text"
                    required
                  ></b-form-input>
                </div>
              </div>
              <!-- Field 4 end -->
            </div>
            <div class="w-100 step__input">
              <!-- Field 5 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1"
                    >Prfecture ou Province</label
                  >
                </div>
                <div class="col-7">
                  <b-form-select
                    class="step__input__select"
                    v-model="form.prefectureOuProvince"
                    :options="prefectureOuProvinceOptions"
                    @change="changeProvince"
                    required
                  ></b-form-select>
                </div>
              </div>
              <!-- Field 5 end -->
            </div>
            <div class="w-100 step__input">
              <!-- Field 6 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1"
                    >Commune</label
                  >
                </div>
                <div class="col-7">
                  <b-form-select
                    class="step__input__select"
                    v-model="form.commune"
                    :options="communeOptions"
                    :disabled="processing"
                    required
                  ></b-form-select>
                </div>
              </div>
              <!-- Field 6 end -->
            </div>
            <div class="w-100 step__input">
              <!-- Field 7 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1"
                    >Code postale</label
                  >
                </div>
                <div class="col-7">
                  <b-form-input
                    class="step__input__text"
                    v-model="form.codePostale"
                    :min="0"
                    type="number"
                    required
                  ></b-form-input>
                </div>
              </div>
              <!-- Field 7 end -->
            </div>
            <div class="w-100 step__input">
              <!-- Field 8 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1"
                    >Nationalit</label
                  >
                </div>
                <div class="col-7">
                  <b-form-select
                    class="step__input__select"
                    v-model="form.nationalite"
                    :options="countries"
                    required
                  ></b-form-select>
                </div>
              </div>
              <!-- Field 8 end -->
            </div>
            <div class="w-100 text-right">
              <button type="submit" class="button button--green">
                Enregistrer et ajouter un autre Matre d'ouvrage
              </button>
            </div>
          </form>
        </div>
      </div>
      <div class="col-12">
        <div class="home__stage step__block mt-3">
          <div class="step__block__header">
            <div class="step__block__header__content">
              Matre douvrage : Informations
            </div>
          </div>
          <table class="text-center table table-bordered">
            <thead>
              <tr style="background-color: #d58512;color: white;">
                <th class="text-center">N</th>
                <th class="text-center">Initiale</th>
                <th class="text-center">Identifiant</th>
                <th class="text-center">Dnomination</th>
                <th class="text-center">Adresse</th>
                <th class="text-center">Prfecture/Commune</th>
                <th class="text-center">Supprimer</th>
              </tr>
            </thead>
            <tbody>
              <template v-for="(owner, index) in owners">
                <tr :key="index">
                  <td class="text-center">{{ index + 1 }}</td>
                  <td class="text-center">{{ owner.nom }}</td>

                  <td class="text-center">{{ owner.commune }}</td>
                  <td class="text-center">{{ owner.registreNumbero }}</td>

                  <td class="text-center">{{ owner.denomination }}</td>
                  <td class="text-center">{{ owner.adresse }}</td>
                  <td class="text-center">{{ owner.prefectureOuProvince }}</td>
                  <td class="text-center">
                    <button class="button button--green" @click="remove(index)">
                      Supprimer
                    </button>
                  </td>
                </tr>
              </template>
            </tbody>
          </table>
        </div>
      </div>
    </div>
    <div class="row mt-4">
      <div class="col-6 text-left">
        <a
          href="javascript:void(0)"
          @click="prevStep"
          class="button button--blue"
          >Prcdent</a
        >
      </div>
      <div class="col-6 text-right">
        <a
          href="javascript:void(0)"
          @click="nextStep"
          class="button button--blue"
          >suivant</a
        >
      </div>
    </div>
  </div>
</template>

<script>
import { GET_COMMUNE } from "@/core/store/app.module";
import { mapState } from "vuex";
export default {
  name: "Step-1",
  data: () => ({
    owners: [],
    processing: false,
    form: {
      maitredouvrage: "",
      cinPasseport: "",
      nom: "",
      prenom: "",
      registreNumbero: "",
      denomination: "",
      adresse: "",
      prefectureOuProvince: "",
      commune: "",
      codePostale: "",
      nationalite: ""
    }
  }),

  methods: {
    remove(index) {
      this.owners = this.owners.filter(
        (owner, indx) => owner && index !== indx
      );
    },
    nextStep() {
      this.$emit("go-next");
    },
    prevStep() {
      this.$emit("go-back");
    },
    async changeProvince(id) {
      this.processing = true;
      this.form.commune = "";
      await this.$store.dispatch(GET_COMMUNE, id);
      this.processing = false;
    },
    submitForm() {
      const temp = {
        maitredouvrage: "",
        cinPasseport: "",
        nom: "",
        prenom: "",
        registreNumbero: "",
        denomination: "",
        adresse: "",
        prefectureOuProvince: "",
        commune: "",
        codePostale: "",
        nationalite: ""
      };
      const newVar = Object.assign({}, this.form);
      this.owners.push(newVar);
      this.form = temp;
    }
  },
  computed: {
    ...mapState({
      maitreDouvrageOptions: state => [
        { value: "", text: "Veuillez selectionnez" },
        ...state.app.selectOptions.maitreDouvrage
      ],
      communeOptions: state => [
        { value: "", text: "Veuillez selectionnez" },
        ...state.app.selectOptions.commune
      ],
      prefectureOuProvinceOptions: state => [
        { value: "", text: "Veuillez selectionnez" },
        ...state.app.selectOptions.prefectureOuProvince
      ],
      countries: state => [
        { value: "", text: "Veuillez selectionnez" },
        ...state.app.selectOptions.countries
      ]
    })
  }
};
</script>

<style lang="scss" scoped></style>
